import React from 'react'

export default ({game}) => (
  <div className="gameInfo">
    <div className="gameInfo__field">
      <div className="gameInfo__label">Počet hráčů</div>
      <div className="gameInfo__value">{game.players}</div>
    </div>
    <div className="gameInfo__field">
      <div className="gameInfo__label">Délka hry</div>
      <div className="gameInfo__value">{game.time}</div>
    </div>
    <div className="gameInfo__field">
      <div className="gameInfo__label">Věk</div>
      <div className="gameInfo__value">{game.age}</div>
    </div>
    <div className="gameInfo__field">
      <div className="gameInfo__label">Rok vydání</div>
      <div className="gameInfo__value">{game.publishYear}</div>
    </div>

    {game.gallery && (
      'Fotogalerie'
    )}

    {game.tags && (
      'Tagy'
    )}

    {game.extLinks && (
      <div className="gameInfo__extLinks">
        {game.extLinks.bgg && (
          <a href={game.extLinks.bgg} target="_blank" rel="noreferrer noopener">
            <img className="bggLogo" src="/images/bgg.svg" alt="Profil hry na BoardGameGeek.com" />
          </a>
        )}
        {game.extLinks.zatrolene && (
          <a href={game.extLinks.zatrolene} target="_blank" rel="noreferrer noopener">
            <img className="zhLogo" src="/images/zatrolene-hry.png" alt="Profil hry na ZatroleneHry.cz" />
          </a>
        )}
      </div>

    )}
  </div>
)


